  z-index: 100000; }
  #searchModal .modal-dialog {
    position: absolute;
    left: 25%;
    top: 25%;
    width: 50% !important;
    height: 50% !important;
    margin-top: 0px !important; }
  #searchModal .modal-content {
    padding: 10px 15px;
